.header {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  padding: .5rem 0;
  border-bottom: .3rem solid black;
  background: #ff4545;

  h1 {
    text-align: center;
    font-size: 2rem;
    color: white;
  }

  a {
    color: white !important;
    text-decoration: none !important;
  }
}

.headerSafeZone {
  height: 3.7rem;
}
